import {  CardMedia, Stack, Typography }
  from '@mui/material'
import React, { useContext, } from 'react'

import { Context } from '../App'
import { QuestionMark } from '@mui/icons-material'

import Cards from "./reuseable/Cards"
import Footer from './reuseable/Footer';

import { useEffect } from 'react'

import IpFinderBlog from './reuseable/IpFinderBlog'

import {Helmet} from "react-helmet";
let sx = {
  CardMedia: {
    height: 300,
    width: "100%",
    objectFit: "contain",
    objectPosition: "center",
    mt: 2,




  },

}
function Home() {
  let { darkMode, setIndex } = useContext(Context)


  useEffect(() => {
    setIndex(0)
  }, [])
  return (
    <>
   <Helmet>
                <meta charSet="utf-8" />
                <title>IP Finder</title>
                <link rel="canonical" href="http://mysite.com/example" />
                <meta name="keywords" content="IP address, geolocation, IP lookup, connectivity, online tools, internet protocol, IP details, location finder, digital footprint, IP tracker, URL shortener, short link, link management, latitude, longitude, coordinates finder"/>

                <meta name="description" content="Explore the power of our IP Finder, a robust geolocation tool that seamlessly uncovers the details of any IP address. With our IP Address Lookup, discover precise location insights, ISP information, and more. Effortlessly resolve connectivity issues and optimize your online experience. Trust our Geolocation Tool for accurate results, making IP-related tasks a breeze. Unlock the potential of streamlined IP discovery with our intuitive IP Finder, your go-to solution for comprehensive and efficient IP address details."





/>
            </Helmet>


      <Stack

        alignItems={"center"}
        height={"100%"}
        bgcolor={"primary.main"}
        mt={3}
        pb={2}
      >
        <CardMedia

          image={darkMode ? 'images/HomeImageDr.webp' : 'images/HomeImageLr.webp'}
          about="it's related image of our idea"
          component={"img"}
          sx={sx.CardMedia}
          alt="it's related image of our idea and services."
        />
        <Typography
          component={"h2"}
          variant='h5'
          textTransform={"capitalize"}
          width={"100%"}
          textAlign={"center"}

          sx={{
            fontSize: {
              xs: 20,
              sm: 26,
              md: 32
            }
          }}

          color="secondary"
        >
   Details at Your Fingertips, Connecting the Digital Dots.

         

        </Typography>




        <Cards heading={"services"}  />

        <Cards icon={<QuestionMark color='secondary'
          fontSize='large' />} heading={"why we"} why_weCred={true} />


<IpFinderBlog home={true}/>
    


      </Stack>
      <Footer />
    </>
  )
}

export default Home