import React, { useContext,  } from 'react'
import { SwipeableDrawer as Dr, FormControlLabel,ListSubheader, IconButton,
     List, ListItem, ListItemButton, ListItemIcon, Stack, Switch, Typography ,Divider} from '@mui/material'

import WbSunnyIcon from '@mui/icons-material/WbSunny';
import DarkModeIcon from '@mui/icons-material/DarkMode';

import CloseIcon from '@mui/icons-material/Close';

import { Context } from '../App';
import { Link } from 'react-router-dom';
import UseGlobalStates from './hooks/useGlobalStates';

let sx = {
    drawer: {
        width: {
            xs: "70vw",
            md: 300,
            lg: "30vw"

        },
        backgroundColor: "primary.main",
        height: "100%",


    },
    ListItemButton: {

        borderRadius: 5,
        height: 50,







    },
    Link: {
        textDecoration: "none",
        width: "100%"
    },
    dStack:{
        height:{
            xs:"100%",
            md:"auto"
        }
    },
    ListSubheader:{
        bgcolor:"primary.main"
    }

}
function Drawer({ drawerOpen, setDrawerOpen }) {
    let { setDarkMode, darkMode,  index,setIndex } = useContext(Context)

    let { routes } = UseGlobalStates()
    let darkModeEnable = () => {
        localStorage.setItem("ipdarkMode",!darkMode)
        setDarkMode(!darkMode)
    
    }
    let onDrawerOpen = () => {
        setDrawerOpen(true)
    }
    let closeDrawer = () => {

        setDrawerOpen(false)
    }
    let indexTab = (i) => {
        setIndex(i)
        closeDrawer()
    }

    return (
        <>



            <Dr
                variant='temporary' open={drawerOpen}

                hideBackdrop
                anchor='left'
                elevation={5}
                onClose={closeDrawer}
                onOpen={onDrawerOpen}


            //   onClick={closeDrawer}





            >
                <Stack

                    component={"div"}
                    bgcolor={"primary.main"}
               sx={sx.dStack}
                >

                    <List sx={sx.drawer}  >
                   <ListSubheader
                sx={sx.ListSubheader}
                   >
                   <Stack
                            height={30}
                            mt={2}
                            mb={2}
                            bgcolor={"secondary"}
                            width={"100%"}
                            direction={"row"}
                            justifyContent={"space-between"}
                            alignItems={"flex-end"}
                           

                        >

                            <FormControlLabel
                                labelPlacement='start'
                                control={<Switch  sx={{

                                }} onChange={darkModeEnable}
                                checked={darkMode}
                                    size='medium'
                                    color='secondary' />} label={darkMode ? <DarkModeIcon fontSize='small' color='secondary' /> :
                                        <WbSunnyIcon fontSize='small' color='secondary' />} />

                            <IconButton onClick={closeDrawer}>
                                <CloseIcon color='secondary' fontSize='medium' />
                            </IconButton>
                        </Stack>
                   </ListSubheader>
                   <Divider/>

                        {
                            routes.map((route, i) => (<>
                                <ListItem
                                    key={i}


                                >
                                    <Link style={sx.Link} to={route.path}>
                                        <ListItemButton

                                            sx={index === i ? {

                                                ...sx.ListItemButton,
                                                bgcolor: "secondary.main",
                                                transition: "1s",
                                                ":hover": {
                                                    bgcolor: "secondary.light"
                                                }


                                            } : sx.ListItemButton}
                                            onClick={() => indexTab(i)}
                                        >

                                            <ListItemIcon

                                            >
                                                {
                                                    index === i ? route.indexTabIcon : route.icon
                                                }
                                            </ListItemIcon>





                                            <Typography
                                                textTransform={"capitalize"}
                                                color={index === i ? "primary" : "secondary"}
                                                fontSize={{ sx: 12 }}


                                            >
                                                {route.name}
                                            </Typography>


                                        </ListItemButton>
                                    </Link>
                                </ListItem>

                            </>
                            ))
                        }
                        {/* <Stack
               mt={2}
             bgcolor={"primary.main"}
             mb={1}
                >
                    <ListItem


                    >
                        <ListItemButton
                            sx={sx.ListItemButton}
                        >

                            <ListItemIcon

                            >
                                <LogoutIcon fontSize='large' color='secondary' />
                            </ListItemIcon>
                            <Typography
                                textTransform={"capitalize"}
                                color={"secondary"}

                            >


                                logout
                            </Typography>
                        </ListItemButton>

                    </ListItem>

                </Stack> */}
                    </List>

                </Stack>


            </Dr>


        </>

    )
}

export default Drawer