import React, { useContext, useEffect } from 'react'
import Input from './reuseable/Input'
import {Stack,Typography} from "@mui/material"
import Cards from './reuseable/Cards'
import Footer from './reuseable/Footer'
import Tables from './reuseable/Tables'
import useGlobalStates from './hooks/useGlobalStates'

import SnackBars from './reuseable/SnackBars'
import Content from './reuseable/Content'
import { Context } from '../App'
import Map from './reuseable/Map'
import OtherContents from './reuseable/OtherContents'

function IpToLocation() {
  // window.scrollTo({
  //   top: 0,
  //   left: 0,
  //   behavior: "smooth",
  // });
let {services} =useGlobalStates()
let {setIndex,setSsnackOpen, setFsnackOpen,}=useContext(Context)
useEffect(()=>{
  setIndex(1)
  return ()=>{
    setSsnackOpen(false);
    setFsnackOpen(false)
  }
},[])
  return (
<>

<Stack

mt={4}

bgcolor={"primary.main"}
>
    <Typography
    component={"h4"}
    mt={2}
    variant='h5'
    textTransform={"capitalize"}
    textAlign={"center"}
  
    color={"secondary.main"}

    fontSize={{
      xs:17,
      md:25
            
          }}
    >
        find the detailes the basies of ip address
    </Typography>

<Input  index={1}   type="ip" />
<Tables/>
<Map/>
<Content/>
<Cards
heading={"other services"}

/>
<OtherContents
dis={true}
data={
  services[0].blog
}
align={"justify"}
/>
</Stack>
<SnackBars/>
<Footer/>

</>
  )
}

export default IpToLocation