import React, { createContext, useEffect } from 'react'

import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Box } from '@mui/material';
import Cookies from "./components/reuseable/Cookies"
import {

  RouterProvider,
} from "react-router-dom";
import Cookie from "cookiejs"
import useRouter from './components/hooks/useRouter';
import UseGlobalStates from './components/hooks/useGlobalStates';

export const Context = createContext()
function App() {


  let { router } = useRouter()
  const { setDarkMode, darkMode, setTableData, tableData, snackFopen,
    snackSopen, setSsnackOpen, setFsnackOpen, snackFmsg, setFsnackMsg, index,
     setIndex, coordinates, setCoordinates,       
     
  } = UseGlobalStates()

  let values = {
    setDarkMode, darkMode, setTableData, tableData, snackFopen, snackSopen, setSsnackOpen, setFsnackOpen,
    snackFmsg, setFsnackMsg, index, setIndex, coordinates, setCoordinates,       
    
  }
  let mustRender = [tableData, snackFmsg, snackFopen, snackSopen, coordinates,index,]
  useEffect(() => {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth",
    });
    
    (() => localStorage.getItem("ipdarkMode") === "true" ? setDarkMode(true) : setDarkMode(false))()
   


  }, mustRender)
  const theme = createTheme({
    palette: {
      primary: {
        main: darkMode ? "#1B2430" : "#fff",
      },
      secondary: {
        main: darkMode ? "#fdd835" : "#00695c",
        light: darkMode ? "#fadf67" : "#157569"
      },
    },
    // components:{
    //   MuiTextField:{

    //     styleOverrides:{
    //       root:{
    //         border:"1px solid red"
    //       }
    //     }
    //   },


    // }
  });
  return (

    <Context.Provider

      value={values}
    >

      <ThemeProvider theme={theme}>

        <Box

          bgcolor={"primary.main"}
          height={"100vh"}
          width={"100vw"}
          boxSizing={"border-box"}
        >
    
        {
            Cookie.get("ipCookie")!=="true"&&<Cookies/>
      
        }  
          <RouterProvider router={router} />

        </Box>




      </ThemeProvider>

    </Context.Provider>
  )
}

export default App