import React, { useContext, useEffect } from 'react'
import { Stack, Paper, Typography, Tooltip, IconButton, TableHead } from "@mui/material"
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import { Context } from '../../App';
import ContentCopyOutlinedIcon from '@mui/icons-material/ContentCopyOutlined';
import DoneOutlinedIcon from '@mui/icons-material/DoneOutlined';
import useFunctions from '../hooks/useFunctions';
import { CopyAllOutlined } from '@mui/icons-material';
import { ListSubheader } from '@mui/material';
function Tables() {
  let { tableData, setTableData,setFsnackOpen,setSsnackOpen } = useContext(Context)
  let { copied, setCopied, copy, copyAllData, copyAll } = useFunctions()
  let TableDataValues = Object.keys(tableData) ? Object.keys(tableData) : []


  useEffect(() => {

    return () => {
      setTableData({});
      setCopied({});
      setFsnackOpen(false);
      setSsnackOpen(false);
    }
  }, [])
  return (
 <>
 
 
<Stack
  mt={3}
  width={"100%"}
  alignItems={"center"}
  bgcolor={"primary.main"}
  maxHeight={500}

>
  <TableContainer
    component={Paper}

    sx={{
      width: {
        xs: "90%",
        md: "60%"
      },
      borderRadius: 3,
      bgcolor:"primary.main"

    }}
  >
{
TableDataValues.length>1&&    


<ListSubheader
component={Stack}
width={"100%"}
direction={"row"}
justifyContent={"flex-end"}

alignItems={"center"}
sx={{
  bgcolor:"primary.main",
  height:60,

}}
>

{
  copyAllData ? 
  <Tooltip placement='right'
  title="Copied All"
  >
<IconButton>
    <DoneOutlinedIcon
      fontSize='large'
      color='secondary'
    />
  </IconButton> 
  </Tooltip>
  : 
<Tooltip
title="Copy All"
placement='right'
>
    <IconButton
  onClick={copyAll}
  >
    <CopyAllOutlined
      fontSize='large'
      color='secondary'
    />
  </IconButton>
</Tooltip>
}



</ListSubheader>
}
    <Table>


      <TableBody
        sx={{

          bgcolor: "secondary.main"

        }}
      >


        {
          TableDataValues?.map((val, i) => (<TableRow
            sx={{

              "&:hover":
              {
                bgcolor: "secondary.light",
                cursor: "pointer",
                transition: "1s"
              }
            }}

            key={i}>
            <TableCell
              align='center'
              sx={{
                color: "primary.main",
                fontSize: 17,
                letterSpacing: 1,
                textTransform: "capitalize",




              }}
            >
              {
                val
              }

            </TableCell>
            <TableCell
              align='center'
              sx={{

                color: "primary.main",
                fontSize: 17,
                letterSpacing: 1,
                textTransform: "capitalize",



              }}
            >
              <Stack
                width={"100%"}
                direction={"row"}
                justifyContent={"space-between"}
                height={"100%"}

                alignItems={"center"}
              >
                <Typography
                width={"80%"}
                sx={{
                  wordBreak:"break-all"
                }}

                >
                  {
                    tableData[val]


                  }


                </Typography>
                {
                 
                  <>
                    {
                      copied[val] ?
                        <Tooltip arrow placement='right' title="copied">
                          <IconButton
                            onClick={() => copy(tableData[val], val)}
                            sx={{
                              width:{
                                xs:"10%",
                                md:50
                              },
                              height:{
                                xs:"10%",
                                md:50
                              },


                            }}
                            
                          >
                            <DoneOutlinedIcon
                              color='primary'
                              fontSize='medium'


                            />
                          </IconButton>
                        </Tooltip>
                        : <Tooltip arrow placement='right' title="copy">
                          <IconButton
                            onClick={() => copy(tableData[val], val)}
                            sx={{
                              width:{
                                xs:"10%",
                                md:50
                              },
                              height:{
                                xs:"10%",
                                md:50
                              },


                            }}
                          >
                            <ContentCopyOutlinedIcon
                              color='primary'
                              fontSize='medium'


                            />
                          </IconButton>
                        </Tooltip>
                    }
                  </>
                }
              </Stack>
            </TableCell>
          </TableRow>))
        }
      </TableBody>
    </Table>
  </TableContainer>
</Stack>
 
 </>
  )
}

export default Tables