import { AppBar, Button, Icon, IconButton, Stack, SwipeableDrawer, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'
import CloseIcon from '@mui/icons-material/Close';
import Cookie from "cookiejs"
let sx = {
  Button: {
    mr: 2,
    borderRadius: 5,
    width: 90,
  
  }
}
function Cookies() {
  let [cookieOpen, setCookieOpen] = useState(true)
  let closeCookies = () => {
    setCookieOpen(false)
  }
  let acceptCookies = () => {
   Cookie.set("ipCookie","true",1)
   setCookieOpen(false)
  }


  return (
    <SwipeableDrawer
      open={cookieOpen}
      anchor='bottom'



    >
      <Stack

        height={90}
        justifyContent={"center"}
        alignItems={"center"}
        bgcolor={"primary.main"}

      >
        <Stack
          direction={"row"}
          justifyContent={"space-between"}
          width={"100%"}
         


        >  <Typography

          pl={3}
          pt={2}
          textTransform={"capitalize"}
          color="secondary.main"
        >Craving a Better Experience? Indulge in Cookies! Click to Accept and Enjoy the Full Flavor of Our Site.
        
        
        
        </Typography>
          <IconButton

            onClick={closeCookies}
          >
            <CloseIcon color='secondary' />
          </IconButton>
        </Stack>
        <Stack direction={"row"} width={"90%"}

          justifyContent={"flex-end"}>
          <Button 
          sx={sx.Button}
           size="small" variant='outlined'
           onClick={acceptCookies}
            color="secondary">


<Typography
              textTransform={"capitalize"}
              color={"secondary"}
              fontSize={13}
              >
              accept
              </Typography>
            </Button>

            <Button 
          sx={sx.Button}
           size="small"
           fontSize={13}
           variant='outlined'
            color="secondary"
            
            onClick={closeCookies}
            >


<Typography
              textTransform={"capitalize"}
              color={"secondary"}
              fontSize={13}
              
              >
              reject
              </Typography>
            </Button>

        
        </Stack>
      </Stack>

    </SwipeableDrawer>
  )
}

export default Cookies