import React, { useContext } from 'react'
import {Snackbar,Alert} from '@mui/material';
import useFunctions from '../hooks/useFunctions';
import { Context } from '../../App';

function SnackBars(success) {
    let {handleFclose,handleSclose}=useFunctions()
    let {snackFopen,snackSopen,snackFmsg}=useContext(Context)

  return (
  <>
{
snackSopen&&
<Snackbar
open={snackSopen}
onClose={handleSclose}
transitionDuration={1000}
autoHideDuration={2000}
sx={{
  width:{
      xs:"70%",
      md:250

  }
}}

>
  <Alert
  variant='filled'
  severity='success'
  sx={{
      width:"100%",
      textTransform:"capitalize"
  }}
  >
sucesssfully completed

  </Alert>
</Snackbar>}
{


snackFopen&&<Snackbar
 open={snackFopen}
 onClose={handleFclose}
 transitionDuration={1000}
 autoHideDuration={2000}
sx={{
   width:{
       xs:"70%",
       md:250

   }
}}
 
 >
   <Alert
   variant='filled'
   severity='error'
   sx={{
       width:"100%",
       textTransform:"capitalize"
   }}
   >
{
    snackFmsg
}

   </Alert>
 </Snackbar>
}


 
  </>
  )
}

export default SnackBars