import React, { useContext, useEffect } from 'react'
import { Stack, Typography } from "@mui/material"
import Footer from './reuseable/Footer'
import Input from './reuseable/Input'
import Cards from './reuseable/Cards'

import useGlobalStates from './hooks/useGlobalStates'
import Content from './reuseable/Content'
import { Context } from '../App'
import Tables from './reuseable/Tables'
import SnackBars from './reuseable/SnackBars'
import OtherContents from './reuseable/OtherContents'
function UrlShortner() {
  let { services } = useGlobalStates()
  let { setIndex,setFsnackOpen,setSsnackOpen } = useContext(Context)
  useEffect(() => {
    setIndex(6)
    return ()=>{
      setSsnackOpen(false);
      setFsnackOpen(false);
      }
  }, [])
  return (

    <>
      <Stack

        mt={4}

        bgcolor={"primary.main"}

      >

        <Typography
          component={"h4"}
          mt={2}
          variant='h5'
          textTransform={"capitalize"}
          textAlign={"center"}

          color={"secondary.main"}

          fontSize={{
            xs: 17,
            md: 25

          }}
        >
          make it catchy , short your urls.
        </Typography>
        <Input index={3} urlS={true} />
        <Tables />
        <Content />
        <Cards heading={"other services"} services={services} />
        <OtherContents
dis={true}
data={services[5].blog}
align={"justify"}
/>
      </Stack>
      <SnackBars />
      <Footer />

    </>
  )
}

export default UrlShortner