import React, { useContext } from 'react'
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import {Card,Typography,Grid,Stack,Button,Box} from "@mui/material"
import ArrowCircleRightSharpIcon from '@mui/icons-material/ArrowCircleRightSharp';
import { Link } from 'react-router-dom';
import { Context } from '../../App';
import useGlobalStates from '../hooks/useGlobalStates';
const sx={
    Button:{
        mr:1,
        borderRadius:3,
        height:35
      },
      Icon:{
        ml:1
       },
       Card:{
        height:{
          sm:"auto",
          sm:160,
          md:160
        },
        width:"95%",
        backgroundColor:"primary.main",
        mb:2,
     
        position:"relative",
        cursor:"pointer",
          transition: "transform 0.5s ease-in-out",
          "&:hover": { transform: "scale3d(1.05, 1.05, 1)" },
          borderColor:"secondary.main",
          borderRadius:5,
          pb:1,
      
      
      },
      ButtonStack:{
        position:{
          xs:"relative",
          md:"absolute"
        },
        bottom:0
      }
}
function Cards({heading,icon,why_weCred }) {
let {  setIndex}=useContext(Context)
let {services,why_we}=useGlobalStates()
let data=why_weCred?why_we:services

  return (
<>

<Box
display={"flex"}

mt={0.5}
width="100vw"
bgcolor={"primary.main"}
flexDirection={"column"}
alignItems={"center"}
>
<Typography
  width={"90%"}
  variant='h4'
  textTransform={"capitalize"}
  letterSpacing={0.9}
  color={"secondary"}
  mb={2.5}
  mt={5}
display={"flex"}
alignItems={"center"}

  >
    {heading?heading:"other services"} 
{
    icon?icon:<ArrowCircleRightSharpIcon
    sx={sx.Icon}
      color='secondary'
      fontSize='large'
      />
}
  </Typography>
<Grid
container
width={"90%"}
spacing={1}
height={"auto"}


>
{
    data.map((service,i)=>(<Grid key={i} xs={12} 
    sm={6}    md={4} item>
        <Card
        variant='outlined'
        
        sx={sx.Card}
        >
            <Typography
            
            color={"secondary.main"}
            textAlign={"center"}
            mt={0.9}
            letterSpacing={0.7}
            textTransform={"capitalize"}
          
            >
           {service.name}
            </Typography>
        <Typography
        textAlign={"center"}
        mt={0.5}
        fontSize={14}
        p={1}
        letterSpacing={0.4}
        color={"secondary"}
        >
        {
          service.discreption
        }
        
        
        </Typography>
        
        <Stack
        width={"100%"}
        alignItems={"flex-end"}
    sx={sx.ButtonStack
  
  }
        pb={2}
    
        
        
        >
  {
    !why_weCred&&     <Link
onClick={()=>setIndex(service.index)}
       to={service.path}
       >
        <Button
    size='small'
    variant='text'
    color='secondary'
    sx={sx.Button}
    endIcon={<ArrowForwardIcon
    
    fontSize='medium'
    color='secondary'
    />}
    >

   <Typography
    textTransform={"capitalize"}
    letterSpacing={"0.9"}
    
    >
      go there
    </Typography>

      </Button>
      </Link>
  }
        </Stack>
          </Card>
        </Grid>))
}
</Grid>
</Box>
</>
  )
}

export default Cards