import React, { useContext, useEffect } from 'react'
import What_s_MyIp from './What\'sMyIp'
import { Context } from '../App'

function MyIpDetailes() {
  let {setFsnackOpen,setSsnackOpen}=useContext(Context)
useEffect(()=>{
  return ()=>{
    setSsnackOpen(false);
    setFsnackOpen(false);
    }
},[])

  return (
    <>
    <What_s_MyIp my_ip={false}/>
    
    </>
  )
}

export default MyIpDetailes