import React, { useContext, useEffect } from 'react'
import {Stack,TextField,Button,Typography,CircularProgress} from "@mui/material"

import { Context } from '../App'
import Tables from './reuseable/Tables'
import Content from './reuseable/Content'
import Cards from './reuseable/Cards'
import SnackBars from './reuseable/SnackBars'
import Footer from './reuseable/Footer'
import Map from './reuseable/Map'
import useFunctions from './hooks/useFunctions'
import OtherContents from './reuseable/OtherContents'
import useGlobalStates from './hooks/useGlobalStates'
const sx={
StackForm:{
  width:{
      md:"60%",
      xs:"90%"
  },
  flexDirection:{
      md:"row",
      xs:"column"
  }
},
cordinput:{
  width:{
      xs:"100%",
      md:"35%",
    
    
  },
mb:2,
textTransform:"capitalize",
},
coordButton:{
  height:40,
  width:{
      md:90,
      xs:"100%",
      
  },

  borderRadius:3,

}
}
function CoordinatesToLoc() {
    let {darkMode  , setSsnackOpen, setFsnackOpen,setIndex}=useContext(Context)
    let {services}=useGlobalStates()
    let {coordMsg,coordVal,coordValNull,disableInp,setDisableInp, 
   
        changeCoord,submitCoord,onBlur,setCoordMsg,setCoordVal,setCoordValNull
    }=useFunctions()
    useEffect(()=>{
      setIndex(7)
return ()=>{
  setCoordVal({
    lan:"",
    lat:""
  })
  setCoordMsg({
    lanInpMsg:"",
    latInpMsg:""
  })
  setCoordValNull({
    lanInpMsg:false,
    latInpMsg:false
  })
  setDisableInp(false)
  setFsnackOpen(false)
  setSsnackOpen(false)
}
    },[])
  return (
<>

<Stack
  mt={6}
  height={"auto"}
  alignItems={"center"}
bgcolor={"primary.main"}
  >
<Stack
direction={"row"}
justifyContent={"space-evenly"}
height={70}
mb={2}
mt={5}
component={"form"}
sx={sx.StackForm}
onSubmit={(e)=>{
e.preventDefault();
submitCoord()

}}
>
<TextField
 focused
 autoComplete='on'
 variant='outlined'
 color={'secondary'}
 size='small'
disabled={disableInp}
 type="search"
 inputProps={{
    style: {
      color: darkMode ? "#fdd835" : "#00695c",
      textTransform: "capitalize"


    }
  }}
  onChange={(e)=>changeCoord(e,true)}
 
 sx={sx.cordinput}
 label="enter lat"
 value={coordVal.lat}
 onBlur={()=>onBlur("lat")}
 helperText={
coordValNull.latInpMsg&&   <Typography
color="error"
textTransform={"capitalize"}
letterSpacing={1}
fontSize={"12px"}
mb={1}






>
{
  coordMsg.latInpMsg
}

</Typography>
  }
/>
<TextField
 focused
 autoComplete='on'
 variant='outlined'
 color={'secondary'}
 size='small'
 disabled={disableInp}
 value={coordVal.lan}
 onChange={(e)=>changeCoord(e,false)}
 onBlur={()=>onBlur("lan")}
 helperText={
    coordValNull.lanInpMsg&& <Typography
    color="error"
    textTransform={"capitalize"}
    letterSpacing={1}
    fontSize={"12px"}
    mb={1}
  >
    {
 coordMsg.lanInpMsg
    }

  </Typography>
    
  }
 inputProps={{
    style: {
      color: darkMode ? "#fdd835" : "#00695c",
      textTransform: "capitalize"


    }
  }}
  label="Enter lang"
 type="search"
 
 sx={sx.cordinput}
/>
<Button
sx={sx.coordButton}
variant='contained'
size='large'
color='secondary'
type='submit'
endIcon={
  disableInp&&<CircularProgress
  color='primary'
  size={20}
  thickness={4}


/>
    }
>
{
  !disableInp&&<Typography
  color="primary"
  textTransform={"capitalize"}
  letterSpacing={1}

>

  find


</Typography>
}
</Button>
</Stack>
<Tables/>
<Map/>
<Content/>
<Cards heading={"other services"}/>
<OtherContents
dis={true}
data={services[6].blog}
align={"justify"}
/>
  </Stack>
  <SnackBars/>
  <Footer/>
</>
  )
}

export default CoordinatesToLoc