import React, { useEffect } from 'react'
import {Stack,Typography} from "@mui/material"
import Tables from './Tables'
import Footer from './Footer'
import { useParams } from 'react-router-dom'
import useFunctions from '../hooks/useFunctions'
import Cards from './Cards'
import useGlobalStates from '../hooks/useGlobalStates'
import Map from './Map'

function TrackerDetiles() {
  let {id}=useParams()
  let {services}=useGlobalStates()
let {trackedInfo,delTrackedInfo}=useFunctions()
  useEffect(()=>{
trackedInfo(id)
return ()=>{
  delTrackedInfo(id)
}
  },[])
  return (
<>

<Stack

mt={4}

bgcolor={"primary.main"}
>
    <Typography
    component={"h3"}
    mt={6}
    variant='h3'
    textTransform={"capitalize"}
    textAlign={"center"}
  
    color={"secondary"}

    fontSize={{
      xs:22,
      md:33
            
          }}
    >
       tracked detailes.
    </Typography>
    <Typography
    component={"p"}
    mt={1}
    variant='p'
    textTransform={"capitalize"}
    textAlign={"center"}
  
    color={"error"}

    fontSize={{
      xs:15,
      md:22
            
          }}
    >
       after you close this tab. this data will be deleted !.
    </Typography>
    <Tables
    
    />
    <Map />
    <Cards
    heading={"other services"}
    services={services}
    />
    </Stack>
    <Footer/>

</>
  )
}

export default TrackerDetiles