import React, { useContext, useEffect } from 'react'
import Input from './reuseable/Input'
import {Stack,Typography,} from "@mui/material"

import Cards from './reuseable/Cards';
import Footer from './reuseable/Footer';
import Tables from './reuseable/Tables';
import useGlobalStates from './hooks/useGlobalStates';
import SnackBars from './reuseable/SnackBars';
import Content from './reuseable/Content';
import { Context } from '../App';
import OtherContents from './reuseable/OtherContents';


function SitesToIp() {

let {services}=useGlobalStates()
let {setFsnackOpen,setSsnackOpen,setIndex}=useContext(Context)
useEffect(()=>{
  setIndex(2)
  return ()=>{
    setSsnackOpen(false);
    setFsnackOpen(false);
    }
},[])


  return (
<>

<Stack

mt={4}

bgcolor={"primary.main"}
>
    <Typography
 component={"h4"}
 mt={2}
 variant='h5'
 textTransform={"capitalize"}
 textAlign={"center"}

 color={"secondary.main"}

 fontSize={{
   xs:17,
   md:25
         
       }}
    >
     Discover Website's IP
    </Typography>

<Input index={0}  type="site" />
<Tables/>
<Content/>
<Cards
heading={"other services"}

services={services}
/>
<OtherContents
dis={true}
data={services[1].blog}
align={"justify"}
/>
</Stack>
<SnackBars/>
<Footer/>
</>
  )
}

export default SitesToIp