import React, { useContext } from 'react'
import { Stack, InputAdornment, TextField, Button, Typography, CircularProgress, Tooltip } from "@mui/material"
import { Context } from '../../App'

import useFunctions from '../hooks/useFunctions';
import { useEffect } from 'react';


const sx = {
  type_warning: {
    textTransform: "capitalize",
    color: "#fff",
    width: {
      xs: "90%",
      md: "40%"
    },



  },
  Stack: {
    width: {
      xs: "90%",
      md: 200

    },

    ml: {
      md: 1
    },
    mt: {
      xs: 1
    }

  },
  Button: {
    width: "100%",
    borderRadius: 10,

  }
}
function Input({ type, index,urlS }) {


  let { darkMode, setSsnackOpen, setFsnackOpen, setTableData } = useContext(Context)
  let { inpVal, change, inpValNull, inpMsg, findSite, findIp, disableInp,

    setInpMsg, setInpVal, setInpValNull, setDisableInp, find_location, generate_url,short_urls

  } = useFunctions()

  let method = [findSite, findIp, find_location,short_urls]
  useEffect(() => {
    return () => {
      setDisableInp(false)
      setInpValNull(false)
      setSsnackOpen(false)
      setFsnackOpen(false)
      setTableData({})
      setInpMsg("")
      setInpVal("")
      setTableData({});
      setFsnackOpen(false);
      setSsnackOpen(false)
      
    }
  }, [])
  return (
    <Stack
      mt={4}
      height={100}
    >


      <Stack
        component={"form"}

        width="100%"

        justifyContent={"center"}
        alignItems={"center"}
        flexDirection={{
          xs: "column",
          md: "row"
        }}
        onSubmit={(e) => {
          e.preventDefault();
          method[index](type)
        }}
      >




 <TextField
      focused
      autoComplete='on'
      onChange={change}
      
      value={inpVal}
      inputProps={{
        style: {
          color: darkMode ? "#fdd835" : "#00695c",
          textTransform: "capitalize"


        }
      }}

      variant='outlined'
      color={'secondary'}
      size='small'

      type="search"
      helperText={
        inpValNull && <Typography
          color="error"
          textTransform={"capitalize"}
          letterSpacing={1}
          fontSize={"12px"}
          mb={1}
        >
          {
            inpMsg
          }

        </Typography>}
      label={urlS?"short your url":type === ("site" || "location") ? "enter a wesite name" : "enter a ip adderss"}
      sx={sx.type_warning}
      disabled={disableInp}

      InputProps={{
        endAdornment: type === "location" ? <InputAdornment

          position='end'
        >
          <Tooltip
            title="it will generate a url for you"
            color='secondary'
            arrow

          >
            <Button
              color='secondary'
              size='small'
              onClick={generate_url}

            >
              <Typography
                fontSize={13}
                letterSpacing={0.5}
                textTransform={"capitalize"}
              >
                create
              </Typography>
            </Button>

          </Tooltip>

        </InputAdornment> : null
      }}
    />
        <Stack

          height={"100%"}
          justifyContent={"flex-start"}
          sx={sx.Stack}

        >
          <Button
            variant='contained'
            size='medium'
            color='secondary'
            sx={sx.Button}
            type='submit'
            disabled={disableInp}
            endIcon={
              disableInp && <CircularProgress
                color='primary'
                size={20}
                thickness={4}


              />}

          >
            <Typography
              color="primary"
              textTransform={"capitalize"}
              letterSpacing={1}

            >

              find


            </Typography>

          </Button>

        </Stack>
      </Stack>
    </Stack>
  )
}

export default Input