
import {Stack,CardMedia} from "@mui/material"
import { useContext } from "react";
import { Context } from "../../App";
import { useEffect } from "react";




const Map = ()=>{

  let { coordinates,setCoordinates ,darkMode} = useContext(Context)

  useEffect(()=>{
return ()=>{
  setCoordinates({})
}
  },[])
return(
<>
{
  coordinates.lat &&<Stack
  width={"100vw"}
  height={"auto"}
  alignItems={"center"}
  mt={3}
  sx={{
    mt:{
      xs:3,
    
    }
  }}
  >
          <CardMedia sx={{
            width:{
              xs:"90%",
              md:"60%"
            },
            height:{
              xs:300,
              sm:250
            },
            filter:!darkMode?"grayscale(0%)":"grayscale(100%)"
   
          }} component={"iframe"} image={`https://maps.google.com/maps?q=${coordinates?.lat},
          ${coordinates?.lan}&hl=es;&output=embed`}
       frameborder="0"/>      
  </Stack>
}

</>
  )


}

export default Map