import { useContext, useState } from 'react'
import axios from "axios"
import useGlobalStates from './useGlobalStates';
import { Context } from '../../App';
import extractDomain from "extract-domain"
import ip_validator from "ip-validator"
import isCoordinates from "is-coordinates"
import { Delete } from '@mui/icons-material';


function useFunctions() {
    let [inpVal, setInpVal] = useState("");
    let [inpMsg, setInpMsg] = useState("");
    let [coordVal, setCoordVal] = useState({
        lat: "",
        lan: ""
    })
    let [coordMsg, setCoordMsg] = useState({
        latInpMsg: "",
        lanInpMsg: "",
    })
    let [coordValNull, setCoordValNull] = useState({
        latInpMsg: false,
        lanInpMsg: false,
    })
    let [disableInp, setDisableInp] = useState(false)
    let [copied, setCopied] = useState({})
    let [copyAllData, setCopyAllData] = useState(false)
    let [inpValNull, setInpValNull] =
        useState(false);
    let isSite = /^[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)$/g
    let { url } = useGlobalStates()
    let { setTableData, setSsnackOpen, setFsnackOpen, setFsnackMsg, tableData, setCoordinates, } = useContext(Context)

    const handleSclose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setSsnackOpen(false);
    };
    const handleFclose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

       setFsnackOpen(false);
    };
    let change = (e) => {

        setInpVal(String(e.target.value).toLowerCase())

        return
    };

    let fetchData = async ({ url, method, data }) => {
        setFsnackMsg("")
        setFsnackOpen(false)
        try {
            let resData = await axios({
                method,
                url,
                data
            })
            let copiedObj = {

            }
            if(!resData.data.data){
return
            }
            Object.keys(resData.data.data).forEach((keys, i) => {

                copiedObj[keys] = false
            })

            setCopied(copiedObj)
            return resData;
        } catch (err) {
            setFsnackMsg("try again")
            setFsnackOpen(true)
            return;
        }



    };



    let findSite = async (type) => {

        setInpValNull(false)


        if (inpVal == "" || !inpVal) {
            setInpValNull(true)

            type === "site" ? setInpMsg("please enter a proper website url") : setInpMsg("please enter a proper ip address");
            return false;
        }
        let extractedDomain = extractDomain(inpVal)
        setInpVal(extractedDomain)

        if (!isSite.test(extractedDomain)) {

            setInpValNull(true)
            type === "site" ? setInpMsg("please enter a vaild website url") : setInpMsg("please enter a vaild ip address");
            return
        }
        let httpIsThere = false;
        if (inpVal.startsWith("https") || inpVal.startsWith("http")) {
            httpIsThere = true;
        }
        try {
            let data = {
                url: type === "site" ? url + "/siteTO-ip" : url + "/siteTO-ip",
                method: "post",
                data: {
                    data: {
                        url: extractedDomain,
                        httpIsThere
                    }
                }
            }
            setDisableInp(true)

            // Do your operations

            let value = await fetchData(data)

            if (value.data.error) {

                setInpMsg(value.data.message)
                setInpValNull(true)
                setFsnackOpen(true)
                setDisableInp(false)
                setFsnackMsg(value.data.message)

                return
            }
            setTableData({
                domain: value.data.data.domain,
                active: "it's active",
                ip: value.data.data.ip,
            })
            setSsnackOpen(true)

            setDisableInp(false)
        } catch (error) {
            setFsnackOpen(true)
            setFsnackMsg("try again. the server is busy")
            setDisableInp(false)
        }

    }
    let findIp = async () => {

        setInpValNull(false)


        if (inpVal == "" || !inpVal) {
            setInpValNull(true)
            setInpMsg("please enter a proper ip address")
            return false;
        }


        try {
            if (ip_validator.ipv4(inpVal) || ip_validator.ipv6(inpVal)) {
                let data = {
                    url: url + "ipTO-detailes",
                    method: "post",
                    data: {
                        data: {
                            ip: inpVal,
                        }
                    }
                }
                let value = await fetchData(data);

                if (value?.data?.error || !value) {

                    setInpMsg(value?.data.message || "invalid ip")
                    setInpValNull(true)
                    setFsnackOpen(true)
                    setDisableInp(false)
                    setFsnackMsg(value?.data.message || "try again")

                    return
                }
                setTableData({
                    range: value.data.data.range || "no value found",
                    country: value.data.data.country || "no value found",
                    region: value.data.data.region || "no value found",
                    city: value.data.data.city || "no value found",
                    lat: value.data.data.ll[0] || "no value found",
                    lang: value.data.data.ll[1] || "no value found",
                    metro: value.data.data.metro || "no value found",
                    area: value.data.data.area || "no value found",
                    timezone: value.data.data.timezone || "no value found",
                })
                setSsnackOpen(true)
                setCoordinates({
                    lat: value.data.data.ll[0],
                    lan: value.data.data.ll[1]
                })
                setDisableInp(false)

                return
            }
            setInpValNull(true)
            setInpMsg("please enter a vaild ip address")
        } catch (error) {
            setFsnackOpen(true)
            setFsnackMsg("try again. the server is busy")
            setDisableInp(false)

        }
    }


    let find_My_Ip = async (my_ip) => {
        setDisableInp(true)
        try {
            let value = await fetchData({
                url: url + "my-ip",
                method: "post",

                data: {
                    data: { my_ip }
                }
            })

            if (value?.data.error) {

                setFsnackOpen(true)
                setDisableInp(false)
                setFsnackMsg(value?.data?.message)

                return
            }
            if (my_ip) {
                setTableData({
                    ip: value.data.data.ip,

                })
                setSsnackOpen(true)
                setDisableInp(false)
                return
            }
            setTableData({
                range: value.data.data.range || "no value found",
                country: value.data.data.country || "no value found",
                region: value.data.data.region || "no value found",
                city: value.data.data.city || "no value found",
                lat: value.data.data.ll[0] || "no value found",
                lang: value.data.data.ll[1] || "no value found",
                metro: value.data.data.metro || "no value found",
                area: value.data.data.area || "no value found",
                timezone: value.data.data.timezone || "no value found",
            })
            setSsnackOpen(true)
            setDisableInp(false)
            setCoordinates({
                lat: value.data.data.ll[0],
                lan: value.data.data.ll[1]
            })
        } catch (error) {
            setFsnackMsg("try again.")
            setFsnackOpen(true)
            setDisableInp(false)
        }

    }
    let find_location = async () => {
        setInpValNull(false)


        if (inpVal == "" || !inpVal) {
            setInpValNull(true)
            setInpMsg("please enter a proper website url")
            return false;
        }


        try {
            let values = await fetchData({
                data: {
                    data: { url: inpVal },
                },
                url: url + "location-tracker", method: "post"
            })

            setDisableInp(true)
            if (values?.data?.error) {
                setFsnackOpen(true)
                setDisableInp(false)
                setFsnackMsg(values.data.message)
                setInpMsg(values.data.message)

                return
            }
            setInpMsg("")
            setSsnackOpen(true)
            setDisableInp(false)
            setInpValNull(false)
            setTableData({
                ["for you"]: values?.data?.data.url,
                ["tracker Url"]: values?.data?.data.trackerUrl,
            })

        } catch (error) {
            setFsnackMsg("try again.")
            setFsnackOpen(true)
            setDisableInp(false)
        }



    }
    let generate_url = () => {
        let url = ["https://google.com", "https://facebook.com", "https://amazon.com", "https://finder.com"];

        let random_num = Math.ceil(Math.random() * 4)
        setInpVal(url[random_num]);

    }
    let short_urls = async () => {
        setInpValNull(false)


        if (inpVal == "" || !inpVal) {
            setInpValNull(true)
            setInpMsg("please enter a proper website url")
            return false;
        }
        let extractedDomain = extractDomain(inpVal)
        if (!isSite.test(extractedDomain)) {

            setInpValNull(true)
            setInpMsg("please enter a valid website url")
            return
        }
        try {
            setDisableInp(true)
            let values = await fetchData({
                url: url + "short-urls",
                method: "post",

                data: {
                    data: { url: inpVal }
                }
            })

            if (values?.data.error) {
                console.log(values, "error state")
                setFsnackOpen(true)
                setDisableInp(false)
                setFsnackMsg(values?.data?.message)

                return
            }


            setDisableInp(false)

            setTableData({
                ["short url"]: values.data.data.short_url
            })
            setSsnackOpen(true)
        } catch (error) {
            console.log(error, "error in catch block")
            setFsnackOpen(true)
            setDisableInp(false)
            setFsnackMsg("try again.")
        }

    }
    let copy = (text, copiedVal) => {
        setCopied({
            ...copied,
            [copiedVal]: true
        })

        navigator.clipboard.writeText(text);
        setTimeout(() => {
            setCopied({
                ...copied,
                [copiedVal]: false
            })
        }, 2000);
    }
    let copyAll = () => {
        let text = JSON.stringify(tableData);
        text = text.slice(1, text.length - 1)


        navigator.clipboard.writeText(text);
        setCopyAllData(true)
        setTimeout(() => {
            setCopyAllData(false)
        }, 2000);
    }
    let trackedInfo = async (id) => {
        try {
            let values = await fetchData({
                data: {

                },
                url: url + `location-tracker/${id}`, method: "post"
            })

            if (values.error) {

                setFsnackOpen(true)
                setDisableInp(false)
                setFsnackMsg(values.data.message)
                setInpMsg(values.data.message)
                setInpValNull(true)
                return
            }
            setTableData(values.data.data)
            setCoordinates({
                lat: values.data.data.lat,
                lan: values.data.data.lang,
            })
            setSsnackOpen(true)
            setDisableInp(false)

        } catch (error) {
            setFsnackOpen(true)
            setDisableInp(false)
            setFsnackMsg("try again.")


        }

    };

    let delTrackedInfo = async (id) => {
        try {
            let _ = await fetchData({
                data: {

                },
                url: url + `location-tracker/del/${id}`, method: "post"
            })

            // if (values.error) {

            //     setFsnackOpen(true)
            //     setDisableInp(false)
            //     setFsnackMsg(values.data.message)
                
            //     return
            // }
          
  //          setSsnackOpen(true)
           // setDisableInp(false)

        } catch (error) {
            // setFsnackOpen(true)
            // setDisableInp(false)
            // setFsnackMsg("try again.")


        }

    }
    let changeCoord = (e, type) => {
        if (type) {
            setCoordVal({
                ...coordVal,
                lat: e.target.value
            })
            return
        }
        setCoordVal({
            ...coordVal,
            lan: e.target.value
        })
    }
    let onBlur = (type) => {
        setCoordMsg({
            lanInpMsg: "",
            latInpMsg: "",
        })
        setCoordValNull({
            lanInpMsg: false,
            latInpMsg: false
        })


        if (type === "lat" && coordVal.lat === "") {
            setCoordMsg({
                ...coordMsg,
                latInpMsg: "plaese give any value"

            })
            setCoordValNull({
                ...coordValNull,
                latInpMsg: true
            })
            return

        }
        if (type === "lan" && coordVal.lan === "") {
            setCoordMsg({
                ...coordMsg,
                lanInpMsg: "plaese give any value"

            })
            setCoordValNull({
                ...coordValNull,
                lanInpMsg: true
            })
            return

        }
    }
    let submitCoord = () => {
        setCoordMsg({
            lanInpMsg: "",
            latInpMsg: "",
        })
        setCoordValNull({
            lanInpMsg: false,
            latInpMsg: false
        })
        if (coordVal.lan === "" && coordVal.lat === "") {

            setCoordMsg({
                latInpMsg: "plaese give any vales",
                lanInpMsg: "plaese give any value"

            })
            setCoordValNull({

                lanInpMsg: true,
                latInpMsg: true
            })
            return

        }

        if (coordVal.lan === "") {
            setCoordMsg({
                ...coordMsg,
                lanInpMsg: "plaese give any value"

            })
            setCoordValNull({
                ...coordValNull,
                lanInpMsg: true
            })
            return

        }
        if (coordVal.lat === "") {
            setCoordMsg({
                ...coordMsg,
                latInpMsg: "plaese give any value"

            })
            setCoordValNull({
                ...coordValNull,
                latInpMsg: true
            })
            return

        }

        if (!isCoordinates([Number(coordVal.lat), Number(coordVal.lan)])) {
            setCoordMsg({
                latInpMsg: "plaese give any valid coords",
                lanInpMsg: "plaese give any valid coords"

            })
            setCoordValNull({

                lanInpMsg: true,
                latInpMsg: true
            })

            return
        }
        setDisableInp(true)
        setCoordinates({
            lat: coordVal.lat,
            lan: coordVal.lan
        });
        setDisableInp(false)
        setSsnackOpen(true)
    }

    return {
        change, inpVal, findIp, findSite, inpValNull, inpMsg, disableInp,
        setInpMsg, setInpVal, setInpValNull, setDisableInp, handleFclose, handleSclose,
        changeCoord, submitCoord, onBlur,delTrackedInfo,

        find_My_Ip, find_location, generate_url, short_urls, copied, setCopied, copy, copyAllData, copyAll, trackedInfo

        , coordMsg, coordVal, coordValNull, setCoordMsg, setCoordVal, setCoordValNull
    };
}

export default useFunctions