import React, { useContext, useEffect } from 'react'
import { Stack,Typography,CardMedia} from "@mui/material"
import Footer from './reuseable/Footer'
import Cards from './reuseable/Cards'
import { Context } from '../App'

const sx={
  Stack:{
  height:{
        xs:"50vh",
        md:"70vh"
      }
    }}

function ErrorPage() {
  let {setIndex,darkMode}=useContext(Context)
  useEffect(()=>{
    setIndex(null)
  },[])
  return (
<>
<Stack
color={"primary.main"}
alignItems={"center"}
justifyContent={"center"}
sx={sx.Stack}
>
  <CardMedia
  component={"img"}
  height={"60%"}
  width={"100%"}
  sx={{
    objectFit:"contain"
  }}
  src={darkMode?'/images/noPageDr.png':"/images/noPageLr.png"}
  about='no page found'
  
  />

<Typography
    component={"h4"}
  
    variant='h4'
    textTransform={"capitalize"}
    textAlign={"center"}
  
    color={"secondary.main"}

   
    >
        no page found !
    </Typography>

</Stack>
<Stack



>
<Cards/>

</Stack>
<Footer/>

</>
  )
}

export default ErrorPage