import React from 'react'
import { Stack, Typography } from "@mui/material"

function Content({data,align,dis}) {
    let sx={
        Stack:{
            
                mt:{
                    xs:6,
    
                }
            
        },
        title:{
            width: {
                xs: "90%",
                md: align?"90%":"60%"
            },
            fontSize:{
                xs:23,
                md:34
            }
        },
        desc:{
            width: {
                xs: "90%",
                md: align?"90%":"60%"
            }
        }
    
    }
    return (
        <Stack
            mt={2}
            bgcolor={"primary.main"}
            height={"auto"}
            alignItems={"center"}
            width={"100%"}
            pb={1}
            sx={sx.Stack
        
        }
        >
            <Stack

                alignItems={"center"}
                width={"auto"}
             
            >

                <Typography
                    textAlign={align?align:"center"}

                    
                    component={"h4"}
                    variant='h4'
                    color={dis?"error":"secondary"}
                    textTransform={"capitalize"}
                    mt={2}
                    mb={1}
                    sx={sx.title
                }
                >{data &&dis?data.title:data ?data.title+"?.":"our goals"}
                
                </Typography>
                <Typography
  
                    mt={1}
                    textAlign={align?align:"center"}
                    component={"p"}
                    color={dis?"error":"secondary"}
                    textTransform={"capitalize"}
                    lineHeight={2}
                    sx={sx.desc
                
                }
                >
           
{data?data.content:
                    `" Our goal is to simplify the process for you and resolve any IP-related issues you may run into. Our goal is to make the complex world of IP easy to navigate, so that any problems you may be having with connectivity may be easily fixed. In addition to troubleshooting, our mission is to improve and streamline your digital experience by making every interaction with your IP easy and hassle-free. You can rely on us to simplify the complicated, 
                    create opportunities out of obstacles, and guarantee that your connectivity is optimal at all times."`
}
                </Typography>
            </Stack>

        </Stack>
    )
}

export default Content