import React from 'react'
import { createBrowserRouter } from 'react-router-dom'

import Nav from '../reuseable/Nav'
import Home from '../Home'
import { Stack } from '@mui/material'

import IpToDetailes from '../IpToDetails'
import SitesToIp from '../SitesToIp'
import ErrorPage from '../ErrorPage'
import What_s_MyIp from '../What\'sMyIp'

import MyIpDetailes from '../MyIpDetailes'
import IpLocationTracker from '../IpLocationTracker'
import UrlShortner from "../UrlShortner"
import TrackerDetiles from '../reuseable/TrackerDetiles'
import CoordinatesToLoc from '../CoordinatesToLoc'
import IpFinderBlog from '../reuseable/IpFinderBlog'

function useHookes() {


  const router = createBrowserRouter([

    {
      path: "/services/ipTo-detailes",
      element: <>
        <Nav />
        <Stack
      bgcolor={"primary.main"}
          mt={4}

        >
          <IpToDetailes />


        </Stack>

      </>,
    },
    {
      path: "/services/sitesTo-ip",
      element: <>
        <Nav />
        <Stack
      bgcolor={"primary.main"}
          mt={7}

        >
          <SitesToIp />


        </Stack>

      </>,
    },
    {
      path: "/",
      element:
        <>

          <Nav />
          <Stack
            mt={4}
            bgcolor={"primary.main"}

          >
            <Home />

          </Stack>


        </>

    }, {
      path: "/services/my-ip-detailes",
      element:
        <>

          <Nav />
          <Stack
            mt={4}
            bgcolor={"primary.main"}

          >
            <MyIpDetailes />

          </Stack>

        </>

    },
    {
      path: "/services/location-tracker",
      element:
        <>

          <Nav />
          <Stack
            mt={4}


          >
            <IpLocationTracker />

          </Stack>

        </>

    },
    {
      path: "/services/url-shortner",
      element:
        <>

          <Nav />
          <Stack
            mt={4}

            bgcolor={"primary.main"}
          >
            <UrlShortner />

          </Stack>

        </>
    },
    {
      path: "/services/location-tracker/:id",
      element:
        <>

          <Nav />
          <Stack
            mt={4}
            bgcolor={"primary.main"}

          >
            <TrackerDetiles />

          </Stack>

        </>

    },
    {
      path: "/services/my-ip",
      element:
        <>

          <Nav />
          <Stack
            mt={4}


          >
            <What_s_MyIp my_ip={true} />

          </Stack>

        </>

    },

    ,
    {
      path: "*",
      element: <>
        <Nav />
        <Stack
          mt={4}
          bgcolor={"primary.main"}
        >
          <ErrorPage />
        </Stack>

      </>

    },{
      path: "/services/lat-lang-finder",
      element: <>
        <Nav />
        <Stack
          mt={6}
          bgcolor={"primary.main"}
        >
          <CoordinatesToLoc />
        </Stack>

      </>
    },
    {
      path: "/blogs",
      element: <>
        <Nav />
        <Stack
          mt={6}
          bgcolor={"primary.main"}
        >
          <IpFinderBlog home={false} />
        </Stack>

      </>
    }
  ]);

  return { router }
}

export default useHookes