import React from 'react'
import { Stack } from "@mui/material"
import Content from './Content'

function OtherContents({ data, dis, align }) {

  return (
    <Stack>

      {
        data.map((con, i) => {
          return (

            <Content
              key={i}
              data={con}
              align={align}
            >

            </Content>

          )
        })
      }
      {
        dis && <Content
          data={{
            title: "disclaimer",
            content: `
    " We do not assume any responsibility for your actions.
     This platform is solely intended for educational purposes. If you engage in any activities that infringe upon others' privacy, participate in illegal activities, or conduct any form of disruptive behavior, we explicitly disclaim any promotion or support for such actions. Please use this platform wisely and responsibly. We emphasize the importance of ethical and lawful conduct. Any misuse or violation of these principles is strongly discouraged and may result in consequences as dictated by applicable laws and regulations."
    
    
    
    
    
    
     
     `

          }}
          align={"justify"}
          dis={true}
        />
      }
    </Stack>
  )
}

export default OtherContents