import React, { useContext, useEffect } from 'react'

import { Stack, Typography, CardMedia, Button, CircularProgress } from "@mui/material"
import Cards from './reuseable/Cards'
import Footer from './reuseable/Footer'
import Tables from './reuseable/Tables'
import useGlobalStates from './hooks/useGlobalStates'

import SnackBars from './reuseable/SnackBars'
import { Context } from '../App'
import useFunctions from './hooks/useFunctions'
import Map from './reuseable/Map'
import OtherContents from './reuseable/OtherContents'


const sx = {
  CardMedia: {
    height: 300,
    width: "100%",
    objectFit: "contain",
    objectPosition: "center",
    mt: 2,
  }
}
function What_s_MyIp({ my_ip }) {

  let { find_My_Ip, disableInp, setDisableInp } = useFunctions();
  let { services, setTableData, } = useGlobalStates();
  let { darkMode,setIndex,setFsnackOpen,setSsnackOpen } = useContext(Context);

  useEffect(() => {
    setTableData({})
  if(my_ip){
    setIndex(3)

    return
  }
  setIndex(4)

    return () => {

      setTableData({})
      setDisableInp(false)

        setSsnackOpen(false);
        setFsnackOpen(false);
        
    }
  }, [my_ip,])
  return (
    <>



      <Stack

        mt={4}

        bgcolor={"primary.main"}
      >
        <CardMedia

          image={darkMode ? "/images/my_ipDr.webp" : "/images/my_ipLr.webp"}
          about="it's related image of our idea"
          component={"img"}
          sx={sx.CardMedia}
        />
        <Typography
          component={"h4"}
          mt={2}
          variant='h5'
          textTransform={"capitalize"}
          textAlign={"center"}

          color={"secondary.main"}

          fontSize={{
            xs: 17,
            md: 25

          }}
        >
          find my {
            my_ip ? "ip" : "ip address details"
          }
        </Typography>
        <Stack
          width={"100%"}
          alignItems={"center"}
        >


          <Button
            disabled={disableInp}
            color='secondary'
            variant='outlined'
            size='large'
            onClick={() => find_My_Ip(my_ip)}
            endIcon={
              disableInp && <CircularProgress
                color='secondary'
                size={20}
                thickness={4}


              />}
            sx={{
              mt: 1.5,
              width: {
                xs: "90%",
                md: 200
              }
            }}
          >
            <Typography
              textTransform={"capitalize"}
              letterSpacing={0.9}
              color={"secondary"}
            >

              find
            </Typography>
          </Button>


        </Stack>
        <Tables />
        {
          !my_ip&&<Map/>
        }
        <Cards
          heading={"other services"}
          services={services}
        />
        {
          !my_ip?
          <OtherContents
          dis={true}
        
          align={"justify"}
          data={services[3].blog}
          />:<OtherContents
          dis={true}
       data={services[2].blog}
          align={"justify"}
          />
        }

      </Stack>
      <Footer />
      <SnackBars />


    </>
  )
}

export default What_s_MyIp